import React from 'react';
import { HStack, Image, Text, Heading, Center, Box, Link } from 'native-base';
const source = require('../assets/images/logo.png');

const SocialItem = ({src, text, href}) => {
    return (
        <HStack borderWidth={2} borderColor="lightBlue.400" borderStyle="double" >
            <Image src={src} size="50px" />
            <Link href={href} >
                <Text fontFamily="body" color="white" fontSize="md" >
                    {text}
                </Text>
            </Link>
        </HStack>
    );
};

const SocialMedia = () => {
    return (
        <Box borderWidth={2} borderColor="lightBlue.400" borderStyle="dashed" >
            <Center>
                <Heading fontFamily="heading" color="white" >
                    My Social Media
                </Heading>
            </Center>
            <HStack>
                <SocialItem src={source} href={"https://github.com/jakkunight/"} text={"GitHub"} />
            </HStack>
        </Box>
    );
};

export default SocialMedia;